import axios from "axios";
import router from '@/router';
import AuthHelper from './helper/AuthHelper';
const url = '/api/users/';

class UserService{
    // Get User
    static async getUsers(limit, search, page){
        let params = {
            params: {
                limit: limit,
                page: page,
                search: search
            }
        };
        if(search.length <= 0){
            delete params.params.search;
        }
        let header = {...AuthHelper.getHeader(), ...params};
        return axios.get(url, header);
    }
    // Create User
    static async insertUser(formData){
        let datas = {
            data: formData
        }
        let header = {...AuthHelper.getHeader()};
        // let header = {...AuthHelper.getHeader(), ...datas};
        return axios.post(url, formData, header);
        // return axios.User(url, {text});
    }

    // Delete User
    static deleteUser(id){
        return axios.delete(`${url}${id}`);
    }
}

export default UserService;