<template>
  <div>
    <CRow alignHorizontal="end">
      <CCol sm="2"><router-link :to="{ path: 'create'}"><CButton color="primary">{{tblDetails.buttonsInfo.name}}</CButton></router-link></CCol>
      <CCol sm="6"></CCol>
      <CCol sm="2">
        <CSelect
          horizontal
          :options="showLimit"
          placeholder="Show"
          :value.sync="searchDetails.showLimitValue"
          @change="gotoPage($event, 'reload')"
        />
      </CCol>
      <CCol sm="2">
        <CInput
          horizontal
          placeholder="Search Keyword"
          v-model="searchDetails.searchKeyword"
          @change="gotoPage($event, 'reload')"
          id="searchInput"
        />
      </CCol>
    </CRow>
    <table class="table table-striped">
      <thead>
        <tr>
          <th scope="col" v-for="(header, name) in tblDetails.header" :key="name">{{name}}</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="tblData in tblDetails.dataList" :key="tblData._id">
          <td v-for="(header, index) in tblDetails.header" :key="index">{{tblData[header]}}</td>
        </tr>
        <tr v-if="tblDetails.dataList.length <= 0">
          <td :colspan="Object.keys(tblDetails.header).length">No data</td>
        </tr>
      </tbody>
    </table>
    <nav v-if="searchDetails.pagination.totalPage > 1" aria-label="">
      <ul class="pagination justify-content-end">
        <li class="page-item" :class="{'disabled' : searchDetails.pagination.activePage == 1}" @click="gotoPage($event, 'prev')">
          <span class="page-link">Previous</span>
        </li>
        <li v-for="(c, i) in searchDetails.pagination.totalPage" :key="i+1" class="page-item" :class="{'active' : i+1 == searchDetails.pagination.activePage}" @click="gotoPage($event, i+1)"><a class="page-link" href="#">{{i+1}}</a></li>
        <li class="page-item" :class="{'disabled' : searchDetails.pagination.activePage == searchDetails.pagination.totalPage}" @click="gotoPage($event, 'next')">
          <a class="page-link" href="#">Next</a>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'TableList',
  props:{
    tblDetails: Object,
    searchDetails: Object
  },
  data () {
    return {
      showLimit: [1,5,10,15,20],
      usersOpened: null,
    }
  },
  computed: {
    fields () {
      return [
        { key: 'key', label: this.username, _style: 'width:150px'},
        { key: 'value', label: '', _style: 'width:150px;' }
      ]
    },
    userData () {
      const id = this.$route.params.id
      const user = usersData.find((user, index) => index + 1 == id)
      const userDetails = user ? Object.entries(user) : [['id', 'Not found']]
      return userDetails.map(([key, value]) => { return { key, value } })
    },
    visibleData () {
      return this.userData.filter(param => param.key !== 'username')
    },
    username () {
      return this.userData.filter(param => param.key === 'username')[0].value
    }
  },
  methods: {
    gotoPage(event, pageNo){
      if( isNaN(pageNo) ){
        // Prev and Next function
        if( event.currentTarget !== undefined && event.currentTarget.classList.contains('disabled')){
          // Prevent unlimited click 'Next and Prev'
          return;
        }
        switch(pageNo.toString().trim()){
          case 'prev':
            this.searchDetails.pagination.activePage = (this.searchDetails.pagination.activePage > 1) ? this.searchDetails.pagination.activePage -=1 : 1; 
            break;
          case 'next':
            this.searchDetails.pagination.activePage = (this.searchDetails.pagination.activePage < this.searchDetails.pagination.totalPage) ? this.searchDetails.pagination.activePage +=1 : this.searchDetails.pagination.activePage; 
            break;
          case 'reload':
            this.searchDetails.pagination.activePage = 1
            break;
        }
      }else{
        if(event.currentTarget.classList.contains('active') !== true){
          this.searchDetails.pagination.activePage = pageNo;
        }
      }
      this.$emit('getData');
    },
    goBack() {
      this.usersOpened ? this.$router.go(-1) : this.$router.push({path: '/users'})
    }
  }
}
</script>
