<template>
  <CRow>
    <CCol col="12" xl="12">
      <CCard>
        <CCardHeader>
          Users
        </CCardHeader>
        <CCardBody>
          <TableList :tblDetails="tblDetails" :searchDetails="searchDetails" @getData="updateCurrentUrl" />
        </CCardBody>
      </CCard>
    </CCol>
  </CRow>
</template>

<script>
import UserService from '../../services/UserService'
import TableList from '../custom/TableList'
export default {
  name: 'Users',
  components:{
    TableList
  },
  data () {
    return {
      tblDetails: {
        buttonsInfo: {
          'name': 'Create user',
          'link': '/users/create'
        },
        header: {
          'id': '_id',
          'name': 'name',
          'data': 'date'
        },
        dataList: [],
      },
      searchDetails:{
        showLimitValue: (this.$route.query.sl) ? Number.parseInt(this.$route.query.sl) : 5,
        searchKeyword: (this.$route.query.sk) ? this.$route.query.sk : '',
        pagination: {
          'totalPage': 0,
          'activePage': (this.$route.query.p) ? Number.parseInt(this.$route.query.p) : 1
        },
      }
    }
  },
  created() {
    // Reroute when no querystring
    if( (this.$route.query.sl === undefined) || (this.$route.query.p === undefined)){
      this.updateCurrentUrl();
    }else{
      this.getUser();
    }
  },
  methods: {
    rowClicked (item, index) {
      this.$router.push({path: `users/${index + 1}`})
    },
    pageChange (val) {
      this.$router.push({ query: { page: val }})
    },
    updateCurrentUrl(){
      if( !((this.$route.query.sl == this.searchDetails.showLimitValue) && (this.$route.query.p == this.searchDetails.pagination.activePage) && ( this.$route.query.sk == this.searchDetails.searchKeyword)) )
        this.$router.push({name: 'Users',  query: {sl: this.searchDetails.showLimitValue, p: this.searchDetails.pagination.activePage, sk: this.searchDetails.searchKeyword}});
      this.getUser();
    },
    async getUser(){
      try{
        let currentUserDatas = UserService.getUsers(this.searchDetails.showLimitValue, this.searchDetails.searchKeyword, this.searchDetails.pagination.activePage);
        currentUserDatas.then( (res) =>{
          this.tblDetails.dataList = res.data.dataList;
          this.searchDetails.pagination.totalPage = res.data.totalPage;
        });
      }catch(error) {
        console.log(error);
      };
    }
  }
}
</script>
